.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 800px;
  margin: auto;
  font-size: 18px;
}

.game-info, .game-settings {
  width: 150px;
}

.button {
  width: 100px;
  margin: 5px auto;
  border: 3px solid #000;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
}

.button:hover {
  border: 3px solid #fce;
}

.button:active {
  border: 3px solid #f9d;
}

.board {
  width: 500px;
  margin: auto;
}

.board-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: horizontal;
      flex-direction: horizontal;
}

.board-tile {
  width: 58.5px;
  height: 58.5px;
  border: 2px solid rgba(0,0,0,0);
  padding: auto;
}

.black {
  background-color: #000;
}

.white {
  background-color: #fff;
}

.tile-current {
  background: #f9d;
  background-image: url(./knight-icon.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.tile-marked {
  background: #f9d;
}

.tile-possible {
  cursor: pointer;
}

.tile-possible:hover {
  background: #fce;
}

.tile-possible-marked {
  background: #f9d;
  cursor: pointer;
}

.tile-possible-marked:hover {
  background: #fce;
}

@media (max-width: 840px) {
  .game {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
  }

  .game-info, .game-settings {
    width: 100%;
  }
}

@media (max-width: 560px) {
  .board {
    width: 480px;
  }

  .board-tile {
    width: 56px;
    height: 56px;
  }
}

@media (max-width: 520px) {
  .board {
    width: 440px;
  }

  .board-tile {
    width: 51px;
    height: 51px;
  }
}

@media (max-width: 480px) {
  .board {
    width: 400px;
  }

  .board-tile {
    width: 46px;
    height: 46px;
  }
}

@media (max-width: 440px) {
  .board {
    width: 360px;
  }

  .board-tile {
    width: 41px;
    height: 41px;
  }
}

@media (max-width: 400px) {
  .board {
    width: 320px;
  }

  .board-tile {
    width: 36px;
    height: 36px;
  }
}

@media (max-width: 360px) {
  .board {
    width: 280px;
  }

  .board-tile {
    width: 31px;
    height: 31px;
  }
}



